.Header{
  height: 6rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px #313131 solid;
  background-color: #313131;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Header-logo {
  -webkit-animation: logo-scale infinite 3s linear alternate;
          animation: logo-scale infinite 3s linear alternate;
  height: 4rem;
  width: 4rem;
  color: #eee;
}

.Header-title {
  font-family: 'Irish Grover', cursive;
  font-size: 2.5em;
  margin: 0 2rem;
  color: #eee;
}

@-webkit-keyframes logo-scale {
  from { -webkit-transform: scale(.8,.8); transform: scale(.8,.8); }
  to { -webkit-transform: scale(1.5,1.5); transform: scale(1.5,1.5); }
}

@keyframes logo-scale {
  from { -webkit-transform: scale(.8,.8); transform: scale(.8,.8); }
  to { -webkit-transform: scale(1.5,1.5); transform: scale(1.5,1.5); }
}



